import React from 'react';
import s from './Post1.module.css';


const Post1 = (props) => {
    console.log(props.message);
    return <div className={s.item}>

        <img src='data:image/jpeg;base64,'/>
        {props.message}

        <div>
           <span>Like</span> {props.likesCount}
        </div>
    </div>
}
export default Post1;