import React from 'react';
import s from './ProfileInfo.module.css';
import p from '../Profile.module.css';


const ProfileInfo = () => {
    return <div >
        <div className={p.content}>
            <img
                src='data:image/jpeg;base64,'/>
        </div>

        <div className={s.descriptionBlock}>
            ava + description
        </div>
    </div>
}
export default ProfileInfo;